import React from 'react'
import { inject, observer } from 'mobx-react';
import { Row, Input, Button, Tabs, Upload, message } from 'antd';
import SparepartsTable from './SparepartsTable'
import SparepartsDialog from './SparepartsDialog'
const { TabPane } = Tabs

@inject('SparepartsStore')
@observer

class SparepartsPage extends React.Component {
  state = {
    selectedRowKeys:[]
  }
  render() {
    const { modalChange, modalVisible, columnsType, typeName, spareName } = this.props.SparepartsStore
    const rowSelection = {
      selectedRowKeys:this.state.selectedRowKeys,
      onChange:this.onSelectedKeys
    }
    const props = {
			name: 'file',
			action: '/manage/spare/spare/import',
			headers: {
				authorization: 'authorization-text',
				token: sessionStorage.getItem('selfToken'),
			},
			showUploadList: false,
			accept: '.xlsx,.xls',
			onChange: (info)=>{
        if(info.file.status == 'done'){
          if(info.file.response.code == 0){
            this.onSearchData();
          }
        }
      }
		};
    return <div>
      <Tabs defaultActiveKey="1" onChange={this.changeTabs}>
        <TabPane tab={<div><i className="iconfont icon-type icon-spacing"></i>备件类型</div>} key="1" />
        <TabPane tab={<div><i className="iconfont icon-list icon-spacing"></i>备件列表</div>} key="2" />
      </Tabs>
      <div className="tab-content-bg">
        <Row>
          <Input style={{ width: 250 }} placeholder={columnsType === '1' ? '请输入备件类型名称' : "请输入备件名称"} allowClear value={columnsType === '1' ? typeName : spareName} onChange={this.setValue}></Input>
          <Button type='primary' className="btn-border-purple" style={{ marginLeft: 10 }} onClick={this.onSearchData}>查询</Button>
          <Button type='primary' className='add-btn' style={{ marginLeft: 10 }} onClick={e => this.addNew(columnsType === '1' ? '0' : '1')}>
            <i className='iconfont icon-add icon-spacing' style={{ fontSize: '12px' }}></i>
            新增
          </Button>
          {
            this.props.SparepartsStore.columnsType == '2' && [
              <Button
							// size='small'
							type='primary'
							style={{ marginLeft: 8, fontSize: '14px' }}
							onClick={() => { window.open(`//${window.location.host}/manage/spare/spare/download`) }}
						>
							下载模版
							</Button>,
						<Upload {...props}>
							<Button
								// size='small'
								type='primary'
								style={{ marginLeft: 8, fontSize: '14px' }}
							>
								<i className='iconfont icon-input icon-spacing' style={{fontSize:'12px'}}></i>
								导入
								</Button>
						</Upload>,
						<Button
							// size='small'
							type='primary'
							style={{ marginLeft: 8, fontSize: '14px' }}
							onClick={this.handleExport}
						>
							<i className='iconfont icon-output icon-spacing' style={{fontSize:'12px'}}></i>
							导出
						</Button>
            ]
          }
        </Row>
        <SparepartsTable rowSelection={rowSelection} />
        {modalVisible && <SparepartsDialog />}
      </div>
    </div>;

  }
  onSelectedKeys = (selectedRowKeys)=>{
    this.setState({selectedRowKeys})
  }
  handleExport = ()=>{
    if(this.state.selectedRowKeys.length > 0 ){
      const url = `/manage/spare/spare/export?ids=${this.state.selectedRowKeys.join(',')}`
			window.open(url)
    }else{
      message.warn('请选择导出项')
    }
  }
  addNew = (type) => {
    this.props.SparepartsStore.modalChange(0)
    if (type === '1') this.props.SparepartsStore.getAllType()
  }
  setValue = (e) => {
    const { columnsType } = this.props.SparepartsStore
    columnsType === '1' ? this.props.SparepartsStore.typeName = e.target.value : this.props.SparepartsStore.spareName = e.target.value
  }
  onSearchData = () => {
    const { getData, PageInfo, columnsType, typeName, spareName } = this.props.SparepartsStore
    columnsType === '1' ? getData(1, { typeName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize }) : getData(2, { spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize })
  }
  changeTabs = (e) => {
    this.props.SparepartsStore.tableData = []
    this.props.SparepartsStore.typeName = ""
    this.props.SparepartsStore.spareName = ""
    this.props.SparepartsStore.columnsType = e
    this.props.SparepartsStore.PageInfo = { pageIndex: 1, pageSize: 10 }
    const { getData, PageInfo, columnsType, typeName, spareName } = this.props.SparepartsStore
    columnsType === '1' ? getData(1, { typeName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize }) : getData(2, { spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize })
  }
  componentDidMount() {
    this.props.SparepartsStore.getAllDeviceType()
  }
  componentWillUnmount() {
    this.props.SparepartsStore.clearData()
  }
}

export default SparepartsPage