import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import { message, Form, Input, Select, Radio, Checkbox } from 'antd';
import GlobalModal from 'Components/GlobalModal';
@inject('SparepartsStore')
@observer
class SparepartsDialog extends Component {
    constructor(props) {
        super(props)
        this.state = {}
    }
    render() {
        const { modalVisible, formData, modalType, devTypeList, columnsType, typeList } = this.props.SparepartsStore
        const formLabelCol = { span: 6 }
        const formWrapperCol = { span: 18 }
        const { Option } = Select
        const { getFieldDecorator } = this.props.form;
        return <div>
            {
                modalVisible && <GlobalModal
                    title={modalType === 2 ? '详情' : modalType === 1 ? '编辑' : '新增'}
                    visible={modalVisible}
                    onOk={this.handleSubmit}
                    onCancel={e => this.props.SparepartsStore.modalChange(0)}
                    children={
                        <div>
                            {columnsType === '1' ? <Form onSubmit={this.handleSubmit} layout='horizontal' labelCol={formLabelCol} wrapperCol={formWrapperCol}>
                                <Form.Item label="备件类型名称">
                                    {getFieldDecorator('typeName', {
                                        rules: [{ required: true, message: '请输入备件类型名称' }],
                                        initialValue: formData.typeName || ''
                                    })(
                                        <Input readOnly={modalType === 2} placeholder="请输入备件类型名称" style={{ width: 300 }} />
                                    )}
                                </Form.Item>
                                <Form.Item label="状态">
                                    {getFieldDecorator('typeStatus', {
                                        rules: [{ required: true, message: '请选择状态' }],
                                        initialValue: formData.typeStatus || 0
                                    })(
                                        <Radio.Group disabled={modalType === 2}>
                                            <Radio value={1}>启用</Radio>
                                            <Radio value={0}>禁用</Radio>
                                        </Radio.Group>
                                    )}
                                </Form.Item>
                            </Form> : <Form onSubmit={this.handleSubmit} layout='horizontal' labelCol={formLabelCol} wrapperCol={formWrapperCol}>
                                    <Form.Item label="备件名称">
                                        {getFieldDecorator('spareName', {
                                            rules: [{ required: true, message: '请输入备件类型名称' }],
                                            initialValue: formData.spareName || ''
                                        })(
                                            <Input readOnly={modalType === 2} placeholder="请输入备件类型名称" style={{ width: 300 }} />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="备件编号">
                                        {getFieldDecorator('model', {
                                            rules: [{ required: true, message: '请输入备件编号' }],
                                            initialValue: formData.model || ''
                                        })(
                                            <Input readOnly={modalType === 2} placeholder="请输入备件编号" style={{ width: 300 }} />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="备件类型">
                                        {getFieldDecorator('spareTypeId', {
                                            rules: [{ required: true, message: '请输入备件类型名称' }],
                                            initialValue: formData.spareTypeId || ''
                                        })(
                                            <Select disabled={modalType === 2} placeholder="请输入备件类型名称" style={{ width: 300 }} >
                                                {typeList.map(item => (<Option value={item.id} key={item.id}>{item.typeName}</Option>))}
                                            </Select>
                                        )}
                                    </Form.Item>
                                    <Form.Item label="关联设备型号">
                                        {getFieldDecorator('deviceModelId', {
                                            rules: [{ required: false, message: '请选择关联设备' }],
                                            initialValue: formData.deviceModelId ? formData.deviceModelId.split(',') : ''
                                        })(
                                            <Checkbox.Group disabled={modalType === 2} options={devTypeList} />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="库存预警">
                                        {getFieldDecorator('warnMax', {
                                            rules: [{ required: false, message: '请输入库存预警' }],
                                        })(
                                            <div >
                                                <Input readOnly={modalType === 2} placeholder="最小预警值" value={formData.warnMin} style={{ width: 145 }} onChange={e => this.setValue(e, 0)} />
                                                --
                                    <Input readOnly={modalType === 2} placeholder="最大预警值" value={formData.warnMax} style={{ width: 145 }} onChange={e => this.setValue(e, 1)} />
                                            </div>

                                        )}
                                    </Form.Item>
                                    <Form.Item label="仓储位置">
                                        {getFieldDecorator('address', {
                                            rules: [{ required: false, message: '请输入仓储位置' }],
                                            initialValue: formData.address || ''
                                        })(
                                            <Input readOnly={modalType === 2} placeholder="请输入仓储位置" style={{ width: 300 }} />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="供应商">
                                        {getFieldDecorator('supplier', {
                                            rules: [{ required: false, message: '请输入供应商' }],
                                            initialValue: formData.supplier || ''
                                        })(
                                            <Input readOnly={modalType === 2} placeholder="请输入供应商" style={{ width: 300 }} />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="供货周期(月)">
                                        {getFieldDecorator('period', {
                                            rules: [{ required: false, message: '请输入供货周期(月)' }],
                                            initialValue: formData.period || ''
                                        })(
                                            <Input readOnly={modalType === 2} placeholder="请输入备件类型名称" style={{ width: 300 }} />
                                        )}
                                    </Form.Item>
                                    <Form.Item label="联系电话">
                                        {getFieldDecorator('phone', {
                                            rules: [{ required: false, message: '请输入联系电话' }],
                                            initialValue: formData.phone || ''
                                        })(
                                            <Input readOnly={modalType === 2} placeholder="请输入联系电话" style={{ width: 300 }} />
                                        )}
                                    </Form.Item>
                                </Form>}
                        </div>
                    }
                />
            }
        </div>;
    }
    setValue = (e, type) => {
        if (type) {
            this.props.SparepartsStore.formData.warnMax = e.target.value
        } else {
            this.props.SparepartsStore.formData.warnMin = e.target.value
        }
    }
    //提交表单
    handleSubmit = e => {
        e.preventDefault();
        const { getData, modalChange, addNewType, modalType, addNewSpa, formData, PageInfo, columnsType, typeName, spareName } = this.props.SparepartsStore
        this.props.form.validateFields((err, values) => {
            if (!err) {
                if (columnsType === '1') {
                    //新增类型
                    addNewType({ ...formData, ...values }).then(res => {
                        message.success('保存成功')
                        modalChange(0)
                        getData(1, { typeName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize })
                    })
                } else {
                    if (modalType === 2) {
                        modalChange(0)
                    } else {
                        // 新增备件
                        console.log(values)
                        values.deviceModelId = values.deviceModelId.join(',')
                        addNewSpa({ ...formData, ...values }).then(res => {
                            message.success('保存成功')
                            modalChange(0)
                            getData(2, { spareName, pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize })
                        })
                    }
                }
            }
        });
    };
    componentDidMount() {
        // this.props.SparepartsStore.getProList()
    }
}
const SparepartsDialogForm = Form.create({ name: 'define' })(SparepartsDialog);
export default SparepartsDialogForm;